/* Resetowanie domyślnych styli */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.models {
  padding: 0;
  background-color: #f4f4f4;
}

/* Górny panel (menu) */
.navbar-background {
  background-image: url('/public/image7.jpg'); /* Sprawdź poprawność ścieżki */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 30vh; /* Dopasowane do innych komponentów */
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* Przyciemnienie tła dla lepszej widoczności tekstu */
.navbar-background::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

/* Nawigacja */
.navbar {
  position: absolute;
  top: 0;
  width: 100%;
  padding: 10px 0;
  text-align: center;
  z-index: 1000;
  transition: all 0.3s ease;
}

.navbar-content {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 20px;
  height: 80px;
  position: relative;
}

.logo {
  height: 50px;
  margin-right: auto;
}

.navbar ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}

.navbar li {
  margin: 0 15px;
}

.navbar button {
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(5px);
  border: none;
  color: #fff;
  font-size: 18px;
  padding: 10px 20px;
  cursor: pointer;
  transition: background 0.3s ease;
  display: inline-block;
  border-radius: 5px;
}

.navbar button:hover {
  background: rgba(255, 255, 255, 0.5);
}

/* Tytuł sekcji */
.header-text {
  text-align: center;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  margin-bottom: 20px;
  margin-top: auto;
  padding-bottom: 20px;
}

.header-text h1 {
  font-size: 48px;
  margin: 0;
}

/* Galeria modeli trumien */
.models-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  max-width: 1100px;
  margin: 40px auto;
  padding: 20px;
}

.model {
  text-align: center;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.model:hover {
  transform: scale(1.05);
}

.model img {
  width: 100%;
  max-width: 250px;
  height: auto;
  border-radius: 10px;
}

.model h2 {
  font-size: 20px;
  color: #333;
  margin-top: 10px;
}

/* Stopka */
.footer {
  text-align: center;
  margin-top: 40px;
  padding: 20px;
  background-color: #252525;
  color: #fff;
}

.footer p {
  margin: 5px 0;
}
